import { css } from 'emotion';
import Spin1 from 'reactism/static/path1.png';
import Spin2 from 'reactism/static/path2.png';
import Spin3 from 'reactism/static/path3.png';

const Index = ({ img, size, text, style, ...props }) => {
  const miniStyle = css`
    img.spin {
      width: 200px;
      /* margin-top: -90px; */
    }
    img.spin1 {
      width: 35px;
      /* left: 70px;
      top: 10px; */
    }
    img.spin2 {
      width: 35px;
      /* left: 100px;
      top: -12px; */
    }
    img.spin3 {
      width: 35px;
    }
  `
  const mediumStyle = css`
    img.spin {
      width: 200px;
      /* margin-top: -90px; */
    }
    img.spin1 {
      width: 200px;
      /* left: 70px;
      top: 10px; */
    }
    img.spin2 {
      width: 200px;
      /* left: 100px;
      top: -12px; */
    }
    img.spin3 {
      width: 200px;
    }
  `
  const baseStyle = css`
    text-align: center;
    .wrap-spin {
      position: absolute;
      margin-left: -34px;
      margin-top: -27px;
    }
    img.icons{
      margin: 60px 0px 20px;
    }
    img.spin {
      position: absolute;
      animation-name: spin;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
    }

    img.spin1 {
      animation-duration: 10s;
    }
    img.spin2 {
      animation-duration: 8s;
      transform: rotate(180deg);
    }
    img.spin3 {
      animation-duration: 5s;
    }
    ${ size === 'mini' ? miniStyle : mediumStyle }
    .text-spin {
      position: absolute;
      margin-left: 10px;
      margin-top: 5px;
      font-size: 1.2rem;
      font-weight: 300;
      color: #1798D1;
    }
    @keyframes spin {
      from {
        transform:rotate(0deg);
      }
      to {
        transform:rotate(360deg);
      }
    }
    ${ style }
  `
  return (
    <div className={ baseStyle }>
      <div className="wrap-spin">
        <img className="spin spin3" src={ Spin3 } alt="bg_bs_3"/>
        <img className="spin spin2" src={ Spin2 } alt="bg_bs_2"/>
        <img className="spin spin1" src={ Spin1 } alt="bg_bs_1"/>
        { text && <p className="text-spin">{ text }</p> }
      </div>
      { img && <img className="icons" width={80} src={`/static/${ img }`} alt="image-docoblast"/> }
    </div>
  )
}

export default Index
