import PropTypes from 'prop-types'
import { cx , css } from 'emotion'

const ListGrid = ({ float, cols, margin, autoGrid, autoHeight, banResponsive, volume, style, ...props }) => {
  // counting process for margin
  let contents = (24%volume === 0) ? cols : volume;

  const baseStyle = css`
      width:${ cols*100/24 }%;
      float:${ float };
      ${ autoHeight && `height: ${ cols*100/24 }` };
      ${
        !autoGrid
          ? `margin : ${ typeof margin === 'string' ? margin : margin+'%' };`
          : (
            `
            margin : ${ ( (24%contents)/(24*10)/2 ) * 100}%;
            padding : ${ ( (24%contents)/(24*10)/2 ) * 100}%;
            `
          )
      };
      ${
        !banResponsive
        && `
          @media screen and (max-width: 992px) {
            width:50%;
          }
          @media screen and (max-width: 600px) {
            width:100%;
          }
        `
      };
      ${ style };
    `

  return (
    <div className={ cx(baseStyle , style) }>
      { props.children }
    </div>
  )
}
ListGrid.propTypes = {
  float: PropTypes.string,
  cols: PropTypes.number,
  margin: PropTypes.string || PropTypes.number,
  autoGrid : PropTypes.bool,
  banResponsive: PropTypes.bool
}

ListGrid.defaultProps = {
  float: 'left',
  cols: 12,
  margin: 0,
  autoGrid : false,
  banResponsive: false
}
export default ListGrid
